<template>
  <div class="enterprise padd">
    <div class="top">
      <img src="../../assets/avatar.jpg" alt="">
      <h1 class="large_t tc">企业用户，您好</h1>
    </div>
    <div class="middle">
      <ul>
        <li>
          <input type="text" placeholder="企业名称">
        </li>
        <li>
          <input type="text" placeholder="企业联系人电话">
        </li>
      </ul>
      <p class="tip three_t_l">· 为方便企业订购，填您写上述信息</p>
      <button class="bluebtn" @click="showConfirm=true">下一步</button>
      <a ref="tel" href="tel:400-123-6666" v-show="false"></a>
    </div>
    <confirm v-model="showConfirm" @on-confirm="call()" confirm-text="联系客服" title="得乐客服热线">
      <p style="text-align:center;">400-123-6666</p>  
    </confirm> 
  </div>
</template>

<script>
import { Confirm } from 'vux'

export default {
  name: "Enterprise",
  data() {
    return {
      showConfirm: false
    };
  },
  methods: {
    call(){
      this.$refs.tel.click()
    }
  },
  components: {
    Confirm
  }
};
</script>

<style scoped lang="less">
.enterprise {
  font-size: 0.16rem;
  .top {
    padding-top: 0.2rem;
    img {
      width: 1rem;
      height: 1rem;
      display: block;
      margin: 0 auto;
      border-radius: 50%;
      margin-bottom: 0.1rem;
    }
  }
  .middle {
    ul {
      padding-top: 0.15rem;
      li {
        border-bottom: 0.01rem solid #f2f2f2;
        padding: 0.15rem 0 0.16rem 0;
        input {
          width: 100%;
          border: none;
        }
      }
    }
    .tip {
      padding: 0.15rem 0 0.3rem 0;
    }
  }
}
</style>



